
'use client';

import React, { useState } from 'react';
import Image from 'next/image';

export default function AllProducts() {
  const products = [
    // ... same products array
  ];

  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 5;

  const filteredProducts = products.filter((product) =>
    product.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const totalPages = Math.ceil(filteredProducts.length / itemsPerPage);

  const displayedProducts = filteredProducts.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  const getStatusBadge = (status: 'In Stock' | 'Out of Stock') => {
    const colors = {
      'In Stock': 'bg-green-100 text-green-700',
      'Out of Stock': 'bg-red-100 text-red-700',
    };
    return <span className={`px-3 py-1 text-sm font-medium rounded-full ${colors[status]}`}>{status}</span>;
  };

  return (
    <section className="p-6 bg-gray-50 rounded-lg shadow-lg">
      <h2 className="text-2xl font-semibold text-gray-800 mb-6">All Products</h2>

      <input
        type="text"
        placeholder="Search products..."
        value={searchTerm}
        onChange={(e) => {
          setSearchTerm(e.target.value);
          setCurrentPage(1); // Reset to the first page when searching
        }}
        className="w-full mb-4 p-2 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
      />

      <div className="hidden md:block">
        <table className="w-full table-auto border-collapse bg-white rounded-lg shadow">
          <thead className="bg-gray-100">
            <tr className="text-left text-gray-600 text-sm">
              {['Image', 'Product ID', 'Name', 'Price', 'Category', 'Stock Status', 'Actions'].map((header) => (
                <th key={header} className="px-6 py-3 font-medium">{header}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {displayedProducts.map((product, index) => (
              <tr
                key={product.id}
                className={`${
                  index % 2 === 0 ? 'bg-gray-50' : 'bg-white'
                } hover:bg-gray-100 transition-colors`}
              >
                <td className="px-6 py-4">
                  <Image
                    src={product.image}
                    alt={product.name}
                    width={150}
                    height={150}
                    className="w-12 h-12 rounded-lg object-cover"
                  />
                </td>
                <td className="px-6 py-4 text-sm font-semibold text-gray-700">#{product.id}</td>
                <td className="px-6 py-4 text-sm text-gray-600">{product.name}</td>
                <td className="px-6 py-4 text-sm font-bold text-gray-800">{product.price}</td>
                <td className="px-6 py-4 text-sm text-gray-600">{product.category}</td>
                <td className="px-6 py-4">{getStatusBadge(product.stockStatus)}</td>
                <td className="px-6 py-4">
                  <button className="px-4 py-2 text-sm text-white bg-blue-500 rounded-lg shadow hover:bg-blue-600 transition">
                    View Details
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className="md:hidden">
        {displayedProducts.map((product) => (
          <div
            key={product.id}
            className="mb-4 bg-white rounded-lg shadow p-4 border border-gray-200"
          >
            <Image
              src={product.image}
              alt={product.name}
              width={500}
              height={300}
              className="w-full h-32 object-cover rounded-lg mb-4"
            />
            <div className="flex justify-between items-center mb-2">
              <h3 className="text-lg font-semibold text-gray-700">Product #{product.id}</h3>
              {getStatusBadge(product.stockStatus)}
            </div>
            <p className="text-sm text-gray-600">
              <span className="font-medium">Name:</span> {product.name}
            </p>
            <p className="text-sm text-gray-600">
              <span className="font-medium">Price:</span> {product.price}
            </p>
            <p className="text-sm text-gray-600">
              <span className="font-medium">Category:</span> {product.category}
            </p>
            <button className="mt-3 w-full px-4 py-2 text-sm text-white bg-blue-500 rounded-lg shadow hover:bg-blue-600 transition">
              View Details
            </button>
          </div>
        ))}
      </div>

      {/* Pagination */}
      <Pagination currentPage={currentPage} setCurrentPage={setCurrentPage} totalPages={totalPages} />
    </section>
  );
}
